@import '../Rules/variables';
@import '../Rules/global-rules';
@import '../Rules/anomations';

.tasks {
  max-width: 380px;
  background-color: $white;
  min-height: 100px;
  border-radius: 10px;
  margin: 30px auto 80px;
  padding: 20px 20px 30px;
  h2 {
    margin: 0;
    text-align: center;
    color: $ment-green;
}
ul {
  list-style: none;
  padding-left: 0;
    .item-box {
      display: flex;
      text-align: center;
      align-items: center;
      position: relative;
      justify-content: space-between;
      margin-bottom: 15px;
      position: relative;
      transition: opacity 0.5s ease-in-out;    
      li {
        background-color: white;
        width: 81%;
        text-align: left;
        padding: 5px 15px;
        border-radius: 7px;
        color: $ment-green;
        font-weight: 600;
        cursor: pointer;
        &.done {
          background-color: $ment-green;
          color: white;
          transition: .4s;
          }           
        }
      .delete-icon {
        position: absolute;
        right: 0px;
        top: 0;
        width: 34px;
        height: 100%;
        background-color: white;
        color: $red;
        font-size: 18px;
        font-weight: 600;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 7px;
        cursor: pointer;
        transition: .3s;
        &:hover {
          content: "";
          background-color: $red;
          width: 100%;
          border-radius: 7px;
        }
        &:hover .trash {
          color: #ebf2f2;
        }
      }
    }
  }
}
